@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@300..700&display=swap");

* {
  margin: 10px;
  font-family: "Quicksand";
  color: #333;
}

h1 {
  color: blueviolet;
  margin-bottom: 20px;
  text-align: center;
  font-size: 40px;
  font-weight: 800;
}

p {
  text-align: center;
  font-size: 18px;
}

span.heart {
  color: blueviolet;
}

span.bday {
  color: blueviolet;
  font-weight: 600;
}

#divider {
  background-color: lavender;
  height: 0.5px;
  margin-top: 32px;
}

div.content {
  margin-top: 40px;
  background-color: lavender;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

}

div.card {
  display: block;
  width: 45%;
  text-align: center;
  align-items: center;
  justify-content: center;
  padding: 10px;
}


#thumbnail {
  max-width: 320px;
  max-height: 290px;
  width: 100%;
  object-fit: cover;
}


.pix img {
  max-width: 180px;
  width: 100%;
}